/* ==========================================================================
   WooCommerce Buttons responsive styles - begin
   ========================================================================== */

@include ipad-landscape {

    .woocommerce-page .edgtf-content,
    div.woocommerce {
    
        a.button,
        a.added_to_cart,
        input[type="submit"],
        button[type="submit"]:not(.edgtf-search-submit),
        .wc-forward:not(.added_to_cart):not(.checkout-button) {

        }
        .woocommerce-MyAccount-content {
            .woocommerce-message,
            .woocommerce-Message{
                a {
                    &.button {
                        margin-top: -7px;
                    }
                }
            }
        }
    }
}

@include phone-landscape{
    .woocommerce-page .edgtf-content,
    div.woocommerce {
        .woocommerce-MyAccount-content {
            .woocommerce-message,
            .woocommerce-Message {
                a {
                    &.button {
                        margin-top: 0px;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Buttons responsive styles - end
   ========================================================================== */