/* ==========================================================================
   WooCommerce Cart page responsive styles - begin
   ========================================================================== */

@include ipad-landscape{

	.edgtf-woocommerce-page {

		table.cart {

			td.actions {

				>.button {
					
				}
			}
		}
	}
}

@include ipad-portrait {
	
	.edgtf-woocommerce-page {
		
		table.cart {
			
			td.actions {
				
				.coupon {

					float: none;
					
					input[type="text"] {
						width: 180px;
						margin: 0 17px 0 0;
					}
				}
				.button {
					margin: 10px 0 0 0;
					display: block;
				}
			}
		}
	}
}

@include phone-landscape {

	.edgtf-woocommerce-page{

		table.cart {

			.edgtf-quantity-buttons {

				.edgtf-quantity-input {
					height: 46px;
					width: 30px;
					line-height: 46px;
					margin: 0 18px 0 0;
				}

				.edgtf-quantity-minus,
				.edgtf-quantity-plus {
					height: 23px;
					width: 19px;
					line-height: 23px;
				}
			}

			tr.cart_item{
				td.product-remove{
					a{
						&:after{
							position: relative;
							top: -2px;
							vertical-align: bottom;
						}
					}
				}
			}
		}

	}
	
	.edgtf-woocommerce-page {
		
		table.cart {
			
			tr {
				
				th, td {
					font-size: 12px;
				}
				
				&.cart_item {
					
					td {
						
						&.product-thumbnail {
							width: auto;
							padding-right: 0;
							
							img {
								display: none;
							}
						}
					}
				}
			}
			
			td.actions {
				
				.coupon {
					margin: 0 0 20px;
					
					input[type="text"] {
						width: 160px;
						margin: 0 10px 20px 0;
					}
				}

				.coupon,
				> input {
					float: none;
					display: block;
				}
				.button {
					margin: 0;
				}
			}
		}
	}
}

/* ==========================================================================
   WooCommerce Cart page responsive styles - end
   ========================================================================== */